* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: "montseratt", sans-serit;
}

.weather-section {
    background-image: url(../../images/weather/sky-day.svg);
    background-size: cover;
    background-position: top;
    background-repeat: no-repeat;
    transition: background-image 0.5s ease-in-out;
    -webkit-transition: background-image 0.5s ease-in-out;
}
.weather-section.init {
    background-image: url(../../images/weather/weather.svg),
        url(../../images/weather/sky-day.svg);
    background-size: contain, cover;
    background-position: center;
}

.weather-section.i01d {
    background-image: url(../../images/weather/sun.svg),
        url(../../images/weather/sky-day.svg);
    background-size: contain, cover;
}

.weather-section.i01n {
    background-image: url(../../images/weather/moon.svg),
        url(../../images/weather/sky-night.svg);
    background-size: contain, cover;
}

.weather-section.i02d {
    background-image: url(../../images/weather/few-clouds-day.svg),
        url(../../images/weather/sky-day.svg);
    background-position: bottom, top;
}

.weather-section.i02n {
    background-image: url(../../images/weather/few-clouds-night.svg),
        url(../../images/weather/sky-night.svg);
    background-position: bottom, top;
}

.weather-section.i03d {
    background-image: url(../../images/weather/cloud.svg),
        url(../../images/weather/sky-day.svg);
}

.weather-section.i03n {
    background-image: url(../../images/weather/cloud.svg),
        url(../../images/weather/sky-night.svg);
}

.weather-section.i04d {
    background-image: url(../../images/weather/broken-clouds.svg),
        url(../../images/weather/sky-day.svg);
    background-size: contain, cover;
}
.weather-section.i04n {
    background-image: url(../../images/weather/broken-clouds.svg),
        url(../../images/weather/sky-night.svg);
    background-size: contain, cover;
}

.weather-section.i09d {
    background-image: url(../../images/weather/shower-rain.svg),
        url(../../images/weather/sky-day.svg);
}
.weather-section.i09n {
    background-image: url(../../images/weather/shower-rain.svg),
        url(../../images/weather/sky-night.svg);
}

.weather-section.i10d {
    background-image: url(../../images/weather/rain-day.svg),
        url(../../images/weather/sky-day.svg);
}
.weather-section.i10n {
    background-image: url(../../images/weather/rain-night.svg),
        url(../../images/weather/sky-night.svg);
}

.weather-section.i11d {
    background-image: url(../../images/weather/thunderstorm.svg),
        url(../../images/weather/sky-day.svg);
}
.weather-section.i11n {
    background-image: url(../../images/weather/thunderstorm.svg),
        url(../../images/weather/sky-night.svg);
}

.weather-section.i13d {
    background-image: url(../../images/weather/snow-day.svg);
}
.weather-section.i13n {
    background-image: url(../../images/weather/snow-night.svg);
}

.weather-section.i14d {
    background-image: url(../../images/weather/mist-day.svg);
}
.weather-section.i14n {
    background-image: url(../../images/weather/mist-night.svg);
}
main {
    min-height: 100vh;
    /* background-image: linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0.1),
        rgba(0, 0, 0, 0.1)
    ); */
    padding: 25px;
}

.search-box {
    width: 100%;
    margin: 0 auto 75px;
}

.search-box .search-bar {
    display: block;
    width: 100%;
    padding: 15px;

    appearance: none;
    background: none;
    border: none;
    outline: none;

    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 16px;
    box-shadow: 0px 5px rgba(0, 0, 0, 0.2);

    color: #313131;
    font-size: 26px;

    transition: 0.4s ease;
}

.search-box .search-bar {
    background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location {
    color: #fff;
    font-size: 32px;
    font-weight: 500;
    text-align: center;
    text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
    color: #fff;
    font-size: 22px;
    font-weight: 300;
    font-style: italic;
    text-align: center;
    text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.weather-box .temperature {
    position: relative;
    display: inline-block;
    margin: 30px auto;
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    padding: 15px 25px;

    color: #fff;
    font-size: 100px;
    font-weight: 300;

    text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
    text-align: center;
    box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
    color: #fff;
    font-size: 48px;
    font-weight: 700;
    text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

input {
    text-align: center;
}
